<template>
  <section class="save__treatment__wrap box__shadow__01 vertical__scroll">
    <h3>치료제 저장</h3>
    <ul class="select__wrap">
      <li>
        <dl>
          <dt>과목선택</dt>
          <dd>
            <select
              name=""
              id=""
              v-model="reqTreatmentInfo.subjectId"
              @change="changeCategoryBySubject"
            >
              <option value="">과목 전체</option>
              <option
                :value="subject.codeId"
                v-for="subject in treatmentSubjectList"
                :key="subject.index"
              >
                {{ subject.codeName }}
              </option>
            </select>
          </dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt>카테고리</dt>
          <dd>
            <select name="" id="" v-model="reqTreatmentInfo.category">
              <option value="">카테고리 전체</option>
              <option
                :value="category.codeId"
                v-for="category in treatmentCategoryList"
                :key="category.index"
              >
                {{ category.codeName }}
              </option>
            </select>
            <button class="custom__btn" v-if="false">
              <p>추가</p>
            </button>
          </dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt>제목</dt>
          <dd>
            <input
              type="text"
              class="custom__input"
              v-model="reqTreatmentInfo.name"
            />
          </dd>
        </dl>
      </li>
    </ul>
    <div class="treatment__list__wrap">
      <div class="title__wrap">
        <p>표시순서</p>
        <button class="custom__btn delete" @click="deleteButtonClick">
          <span>
            <img src="@/assets/images/icon/ic-remove.svg" alt="삭제 아이콘" />
          </span>
          <p>비우기</p>
        </button>
      </div>
      <div
        class="treatment__list"
        @dragover="dragOver($event)"
        @drop="drop($event, 'add')"
        ref="treatmentList"
      >
        <!-- item -->
        <div
          class="item box__shadow__03"
          v-for="(item, idx) in treatmentSaveList"
          :key="item.seq"
        >
          <div class="left">
            <p class="num f__bold">{{ idx + 1 }}</p>
            <span class="icon">
              <img :src="item.typeImgSrc" />
            </span>
            <p class="title">{{ item.body }}</p>
          </div>
          <div class="right">
            <dl>
              <dt>{{ item.category }}</dt>
              <dd>{{ item.title }}</dd>
            </dl>
          </div>
        </div>
        <!-- item -->
      </div>
      <div class="save__btn__wrap">
        <button class="custom__btn" @click="addOrUpdateTreatment">
          <span>
            <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
          </span>
          <p>저장</p>
        </button>
      </div>
    </div>
  </section>
</template>
<script>
import data1 from "@/data/remedyManage/data1.json";
import data4 from "@/data/remedyManage/data4.json";
import data5 from "@/data/remedyManage/data5.json";
import data6 from "@/data/remedyManage/data6.json";
import data7 from "@/data/remedyManage/data7.json";
import fileTypeIcon from "@/utill/fileTypeIcon";
import { isNotEmpty } from "@/utill/validator";

export default {
  data() {
    return {
      contentAddFlag: true,
      treatmentSaveList: [],
      treatmentSubjectList: [],
      treatmentCategoryList: [],
      reqTreatmentInfo: {
        subjectId: "",
        category: "",
        name: "",
        useYn: "Y",
      },
      isAddTreament: true,
      medicineList: [],
      aStopTreatmentCategoryList: [],
      mStrongTreatmentCategoryList: [],
    };
  },
  props: {
    dragTreatmentItem: [Object, String],
    digitalMedicineList: Array,
  },
  watch: {
    dragTreatmentItem() {
      // if (this.$env == "development") console.log(this.dragTreatmentItem);
    },
    digitalMedicineList() {
      this.medicineList = this.digitalMedicineList;
      // parent component에서 치료약 리스트 받은 후 치료약 ID에 따라 상세 정보 조회 및 화면 표시
      /*this.$watch(
        () => this.$route.query,
        () => {
          this.getTreatmentInfo();
        },
        { immediate: true }
      );*/
    },
  },
  computed: {
    treatmentManagement() {
      return this.$store.state.treatmentManagement;
    },
  },
  mounted() {
    if (this.$route.query.id) {
      this.isAddTreament = false;
      const treatmentList = data1;
      for (let treatment of treatmentList) {
        if (treatment.id == this.$route.query.id) {
          this.reqTreatmentInfo.subjectId = treatment.subjectId;
          this.changeCategoryBySubject();
          this.reqTreatmentInfo = treatment;
          this.treatmentSaveList = treatment.digitalMedicine;
        }
      }
      this.treatmentSaveList.forEach((obj) => {
        data4.forEach((medicine) => {
          if (medicine.id == obj.id) {
            console.log("굿");
            obj.category = medicine.categoryName;
            obj.title = medicine.typeName;
            obj.seq = obj.order;
            obj.body = obj.name;
            this.addIconByMedicineType(obj);
          }
        });
      });
    }
  },
  created() {
    this.getTreatmentSubjectList();
    this.getAStopTreatmentCategoryList();
    this.getMStrongTreatmentCategoryList();
  },
  methods: {
    addOrUpdateTreatment() {
      let digitalMedicine = [];
      this.treatmentSaveList.forEach((obj, index) => {
        let tempObj = {};
        tempObj.order = index + 1;
        tempObj.id = obj.id;
        digitalMedicine.push(tempObj);
      });
      this.reqTreatmentInfo.digitalMedicine = digitalMedicine;
      if (!this.validateTreatment()) return;
    },
    // 치료제 수정인 경우 치료제 ID를 통해 상세 정보 조회 후 표시
    deleteButtonClick() {
      this.treatmentSaveList = [];
    },
    dragOver(evt) {
      evt.preventDefault();
    },
    drop() {
      if (this.$env == "development") console.log(this.dragTreatmentItem);
      this.treatmentSaveList.map((obj) => {
        if (obj.body === this.dragTreatmentItem.body) {
          this.contentAddFlag = false;
        }
      });

      if (this.contentAddFlag) {
        const dropItemObj = this.dragTreatmentItem;
        this.addIconByMedicineType(dropItemObj);

        this.treatmentSaveList.push(dropItemObj);
      } else {
        alert(`'${this.dragTreatmentItem.body}' 항목이 이미 존재합니다.`);
        this.contentAddFlag = true;
      }
    },
    addIconByMedicineType(obj) {
      obj.typeImgSrc = fileTypeIcon(obj.title);
    },
    getTreatmentSubjectList() {
      this.treatmentSubjectList.push(data5.codeList[0]);
    },
    getAStopTreatmentCategoryList() {
      this.aStopTreatmentCategoryList = data6.codeList;
    },
    getMStrongTreatmentCategoryList() {
      this.mStrongTreatmentCategoryList = data7.codeList;
    },
    // 진료과목에 따라 치료약 카테고리 설정
    changeCategoryBySubject() {
      this.reqTreatmentInfo.category = "";
      // 진료과목 알콜중독
      if (this.reqTreatmentInfo.subjectId == "APP001") {
        this.treatmentCategoryList = this.aStopTreatmentCategoryList;
      } else if (this.reqTreatmentInfo.subjectId == "APP002") {
        // 진료과목 인지장애
        this.treatmentCategoryList = this.mStrongTreatmentCategoryList;
      } else {
        this.treatmentCategoryList = [];
      }
      this.$emit("changeCategoryBySubject", this.reqTreatmentInfo.subjectId);
    },
    validateTreatment() {
      if (!isNotEmpty(this.reqTreatmentInfo.subjectId)) {
        this.$toast.error("진료과목을 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.reqTreatmentInfo.category)) {
        this.$toast.error("카테고리를 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.reqTreatmentInfo.name)) {
        this.$toast.error("치료약 이름을 입력하세요.");
        return false;
      }
      return true;
    },
  },
};
</script>
