<template>
  <main class="add__treatment__main">
    <TreatmentSave
      :dragTreatmentItem="dragTreatmentItem"
      :digitalMedicineList="medicineList"
      @changeCategoryBySubject="changeCategoryBySubject"
    />
    <TreatmentAdd
      @treatmentStartDrag="treatmentStartDrag"
      :digitalMedicineList="medicineList"
      :selectedSubjectId="selectedSubjectId"
    />
  </main>
</template>
<script>
import "@/assets/css/addTreatment.css";
import TreatmentSave from "@/components/AddRemedy/RemedySave";
import TreatmentAdd from "@/components/AddRemedy/MedicineAdd";
import data4 from "@/data/remedyManage/data4.json";

export default {
  data() {
    return {
      dragTreatmentItem: {},
      medicineList: [],
      selectedSubjectId: "",
    };
  },
  components: {
    TreatmentSave,
    TreatmentAdd,
  },
  created() {
    this.getMedicines();
  },
  methods: {
    treatmentStartDrag(param) {
      this.dragTreatmentItem = param;
    },
    getMedicines() {
      this.medicineList = data4.filter((obj) => obj.useYn !== "N");
      this.medicineList.forEach((medicine, index) => {
        medicine.index = index + 1;
      });
    },
    changeCategoryBySubject(subjectId) {
      this.selectedSubjectId = subjectId;
    },
  },
};
</script>
