<template>
  <section class="add__treatment__wrap box__shadow__01 vertical__scroll">
    <h3>치료약 조회</h3>
    <div class="treatment__search__wrap">
      <dl>
        <dt>
          <select ref="categorySelector" v-model="searchCategoryId">
            <option value="">카테고리 전체</option>
            <option
              v-for="category in treatmentCategoryList"
              :key="category.index"
              :value="category.codeId"
            >
              {{ category.codeName }}
            </option>
          </select>
        </dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            placeholder="치료약 검색..."
            ref="medicineNameInput"
            v-model="searchMedicineName"
          />
        </dd>
      </dl>
      <button class="custom__btn" @click="medicineSearch">
        <span>
          <img src="@/assets/images/icon/ic-search.svg" alt="검색 아이콘" />
        </span>
        <p>검색</p>
      </button>
    </div>
    <div class="treatment__list__wrap">
      <dl class="title__wrap" v-if="medicineList.length > 0">
        <dt>검색결과&nbsp;&nbsp;:&nbsp;&nbsp;</dt>
        <dd>{{ medicineList.length }}</dd>
      </dl>
      <!-- treatment__list -->
      <ul class="treatment__list" v-if="medicineList.length > 0">
        <li
          class="box__shadow__03"
          v-for="medicine in medicineList"
          :key="medicine.index"
        >
          <dl
            draggable="true"
            @dragstart="startDrag($event)"
            :data-category="medicine.categoryName"
            :data-seq="medicine.index"
            :data-id="medicine.id"
          >
            <dt>{{ medicine.typeName }}</dt>
            <dd class="f__medium">{{ medicine.name }}</dd>
          </dl>
        </li>
      </ul>
    </div>
  </section>
</template>
<script>
import { getCommonCodeList } from "@/api/code";
import data4 from "@/data/remedyManage/data4.json";
import data8 from "@/data/remedyManage/data8.json";

export default {
  data() {
    return {
      itemObj: {},
      selectedType: "",
      searchedMedicineData: [],
      treatmentCategoryList: [],
      medicineList: [],
      searchCategoryId: "",
      searchMedicineName: "",
    };
  },
  props: {
    //digitalMedicineList: Array,
    selectedSubjectId: String,
  },
  watch: {
    /*digitalMedicineList() {
      this.medicineList = this.digitalMedicineList;
    },*/
    selectedSubjectId() {
      this.getTreatmentCategoryList(this.selectedSubjectId);
    },
  },
  created() {
    this.getTreatmentCategoryList();
    // this.getMedicines();
  },
  computed: {
    treatmentManagement() {
      return this.$store.state.treatmentManagement;
    },
  },
  methods: {
    startDrag(evt) {
      this.itemObj = {};
      console.log(evt);
      this.itemObj.title = evt.srcElement.childNodes[0].textContent.trim();
      this.itemObj.body = evt.srcElement.childNodes[1].textContent.trim();
      this.itemObj.category = evt.srcElement.dataset.category;
      this.itemObj.seq = evt.srcElement.dataset.seq;
      this.itemObj.id = evt.srcElement.dataset.id;
      this.$emit("treatmentStartDrag", this.itemObj);
    },
    medicineSearch() {
      this.searchCategoryId = this.searchCategoryId.trim();
      this.searchMedicineName = this.searchMedicineName.trim();
      this.medicineList = data4;
      this.medicineList.forEach((medicine, index) => {
        medicine.index = index + 1;
      });

      /*this.searchedMedicineData = [];
      if (this.searchCategoryId == "") {
        this.medicineList.forEach((obj) => {
          if (obj.name.includes(this.searchMedicineName)) {
            this.searchedMedicineData.push(obj);
          }
        });
      } else {
        this.medicineList.forEach((obj) => {
          if (
            obj.name.includes(this.searchMedicineName) &&
            obj.category == this.searchCategoryId
          ) {
            this.searchedMedicineData.push(obj);
          }
        });
      }*/
    },
    /*medicineSearch() {
      this.searchedMedicineData = [];
      const selectedCategory =
        this.$refs.categorySelector.options[
          this.$refs.categorySelector.selectedIndex
        ].textContent.trim();
      if (selectedCategory === "카테고리 전체") {
        this.treatmentManagement.medicine.forEach((obj) => {
          if (obj.title.includes(this.$refs.medicineNameInput.value))
            this.searchedMedicineData.push(obj);
        });
      } else {
        this.treatmentManagement.medicine.forEach((obj) => {
          if (
            obj.title.includes(this.$refs.medicineNameInput.value) &&
            obj.category === selectedCategory
          )
            this.searchedMedicineData.push(obj);
        });
      }
    },*/
    getTreatmentCategoryList(subjectId) {
      if (subjectId == "APP001") {
        this.treatmentCategoryList = data8.codeList;
      } else if (subjectId == "APP002") {
        let searchQuery = "mainCommCode=MMCA&useYn=Y";
        getCommonCodeList(searchQuery).then((response) => {
          this.treatmentCategoryList = response.data.response.codeList;
        });
      }
    },
  },
};
</script>
